export default {
  data() {
    return {
			// 输入的金额内容
			money: '',
			// 提现返回的状态
			status: '',
			// 判断用户是否可以提现的标准
			flag: '',
			// 银行卡id
			acc_id: '',
			// 用户可提现的金额
			price: '',
			// 0元与其他金额之间的切换显示
			isPrice: true,
			// 绑定银行卡与有银行卡页面之间的切换显示
			iscard: false,
			// 显示的银行卡信息
			cardList: [],
			// 所有的银行卡信息
			result: [],
			// 用来判断是一开始就有的银行卡还是后来选择的银行卡
			id: '',
			user_id: '',
			wallet_id: ''
		};
	},
	onLoad(options) {
		this.wallet_id = uni.getStorageSync('wallet_id')
		this.user_id = uni.getStorageSync('user_id')
		this.acc_id = options.id
		// console.log(this.acc_id)
		// 获取银行卡信息
		this.getAcc()
		// 获取可提现的金额的接口
		// this.getMoney()
		// if (this.cardList.length == 1) {
		// 	this.iscard = true
		// } else {
		// 	this.iscard = false
		// }	
	},
  methods: {
		// 获取银行卡信息
		getAcc() {
			this.$http.get('/wallet/getAcc', { params: { user_id: this.user_id } }).then( res => {
				this.result = res.data
				if (this.result.length == 0) {
					this.iscard = true
				} else {
					this.iscard = false
					if (this.id == '') {
						this.cardList = []
						this.cardList.push(res.data[0])
						this.id = res.data[0].acc_id
					}
					if (this.acc_id !== undefined) {
						var i = this.result.findIndex(item => {
							return item.acc_id == this.acc_id
						})
						this.cardList = []
						this.cardList.unshift(this.result[i])
					}
				}
			})
		},
		// 获取可提现的金额的接口
		// getMoney() {
		// 	this.$http.get('/wallet/getMoney',{ params: { user_id: 1 } }).then(res => {
		// 		console.log(res)
		// 		if (res.data == 0) {
		// 			this.isPrice = true
		// 		} else {
		// 			this.isPrice = false
		// 			this.price = res.data
		// 		}
		// 	})
		// },
		// 去绑定银行卡页面
		goWalletBindingCard() {
			uni.navigateTo({
        url: '/pages/walletBindingCard/walletBindingCard'
      })
		},
		// 去选择银行卡页面
		goWalletChoiceBankCard() {
			uni.navigateTo({
        url: '/pages/walletChoiceBankCard/walletChoiceBankCard'
      })
		},
		// 发送请求 判断用户金额是否达到提现标准
		// getTrue() {
			// this.cash()
			// this.$http.get('/wallet/getTrue', { params: { money: this.money } }).then(res => { 
			// 	console.log(res)
			// 	this.flag = res.data
			// 	if (this.flag !== false ) {
			// 		this.cash()
			// 	} else {
			// 		uni.showToast({
			// 				title: '不满足提现条件',
			// 				duration: 2000,
			// 				icon: 'none'
			// 		});
			// 	}
			//  })
		// },
		// 提现接口
		cash() {
			// 当有输入金额的时候 金额大于 0 的时候 当可以提现的时候 当有银行卡的时候 可以发起提现 现在缺少一个有银行卡的时候
			if (this.money !== '' && this.money > 0 && this.acc_id !== '') {
				this.$http.get('/wallet/getAccount', { params: { wallet_id: this.wallet_id,acc_id: this.acc_id,money: this.money, user_id: this.user_id } }).then(res => {
					console.log(res)
					if (res.data == 200) {
						uni.showToast({
								title: '提现成功',
								duration: 2000,
								icon: 'none'
						})	
					} else if (res.data == 501) {
						uni.showToast({
								title: '未达到提现条件',
								duration: 2000,
								icon: 'none'
						})	
					} else {
					uni.showToast({
							title: '银行卡号或余额不能为空',
							duration: 2000,
							icon: 'none'
						})
					}
				})
			} else {
				// uni.showToast({
				// 		title: '您提现的金额未达到可提现的金额',
				// 		duration: 2000,
				// 		icon: 'none'
				// });
			}
		}
	},
	computed: {
		isActive() {
			if (this.money === '') {
				return false
			}
			return true
		}
	},
};
